import React from "react";
import Menu from "./Menu";
import "./CSS/Global.css";

export default function Tabela({ dados }) {
	return (
		<div>
			<Menu />
			<h2>Acompanhamento das Informaes de Irrigao</h2>
			<table className="custom-table">
				<thead>
					<tr>
						<th>Nome da Fazenda</th>
						<th>Nmero do Hidrante</th>
						<th>Nmero do Carretel</th>
						<th>Talho</th>
						<th>Operador</th>
						<th>Tamanho do Talho</th>
						<th>Data</th>
						<th>Hora</th>
						<th>Status</th>
					</tr>
				</thead>
				<tbody>
					{dados.map((item, index) => (
						<tr key={index}>
							<td>{item["Nome da Fazenda"]}</td>
							<td>{item["Nmero do Hidrante"]}</td>
							<td>{item["Nmero do Carretel"]}</td>
							<td>{item["Talho"]}</td>
							<td>{item["Operador"]}</td>
							<td>{item["Tamanho do Talho"]}</td>
							<td>{item["Data"]}</td>
							<td>{item["Hora"]}</td>
							<td>
								<div
									className={`status-square ${item.Status}`}
								/>
								{item.Status}
							</td>
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
}
