import React from "react";
import Menu from "./Menu";
import "./CSS/Global.css";

export default function AcompanhamentoTempoReal({ dados }) {
    return (
        <div>
            <Menu />
            <h2>Acompanhamento das Informaes de Irrigao</h2>
            <div>
                {dados.map((item, index) => (
                    <div key={index}>
                        <p>{`Nome da Fazenda: ${item["Nome da Fazenda"]}`}</p>
                        <p>{`Nmero do Hidrante: ${item["Nmero do Hidrante"]}`}</p>
                        <p>{`Nmero do Carretel: ${item["Nmero do Carretel"]}`}</p>
                        <p>{`Data: ${item["Data"]}`}</p>
                        <p>{`Hora: ${item["Hora"]}`}</p>
                        <p>{`Talho: ${item["Talho"]}`}</p>
                        <p>{`Operador: ${item["Operador"]}`}</p>
                        <p>{`Tamanho do Talho: ${item["Tamanho do Talho"]}`}</p>
                        <p>{`Status: ${item["Status"]}`}</p>
                        <hr />
                    </div>
                ))}
            </div>
        </div>
    );
}
